import { Component } from '@angular/core';
import { environment } from '../../../../../environments/environment';
import { HttpBackend, HttpClient } from '@angular/common/http';
import moment from 'moment';
import { BrowserDetectionService } from '../../../core/services/browser-detection.service';

const PLANNING_BOARD_TAG = 'planning-board';

interface PlanningBoardVersion {
	version: number;
}

@Component({
	selector: 'app-planning-board',
	templateUrl: './planning-board.component.html',
	styleUrls: ['./planning-board.component.scss'],
	providers: [
		BrowserDetectionService,
	],
})
export class PlanningBoardComponent {
	private readonly httpClient: HttpClient;

	get isCompatibleWithCustomComponents(): boolean {
		return this.browserDetectionService.isCompatibleWithCustomElements();
	}

	constructor(
		handler: HttpBackend,
		private readonly browserDetectionService: BrowserDetectionService,
	) {
		this.httpClient = new HttpClient(handler);
		
		if (this.isCompatibleWithCustomComponents) {
			this.addPlanningBoardWebComponent();
		}
	}

	private async getVersion(): Promise<number> {
		const now = moment().valueOf();
		const response = await this.httpClient
			.get<PlanningBoardVersion>(`${environment.planning_board_url}/static/version.json?t=${now}`)
			.toPromise();
		return response.version;
	}

	private async addPlanningBoardWebComponent(): Promise<void> {
		if (customElements.get(PLANNING_BOARD_TAG)) {
			return;
		}
		const script = document.createElement('script');
		const version = await this.getVersion();
		script.src = `${environment.planning_board_url}/static/js/main.js?v=${version}`;
		document.body.appendChild(script);
	}
}
